// 组织机构页面相关接口
import request from '@/utils/request'

export function fetchTableList (query) {
  return request({
    url: '/org/list',
    method: 'get',
    params: query
  })
}

export function fetchOrgTree (query) {
  return request({
    url: '/org/tree',
    method: 'get',
    params: query
  })
}
