<template>
  <div class="app-container">
    <el-row :gutter="10">
      <el-col :span="6">
        <div>
          <el-input v-model="filterText" placeholder="快速过滤组织" style="margin-bottom:30px;"/>

          <el-button type="danger" @click="resetChecked">清空选中的Node</el-button>
          <div class="buttons"> 
            <el-button type="success" plain @click="getCheckedNodes">获取选中的Node</el-button>
            <el-button type="success" plain @click="setCheckedNodes">刚才点击了哪些Node？</el-button>
          </div>
          <div class="buttons"> 
            <el-button type="success" plain @click="getCheckedKeys">获取选中的NodeKey</el-button>
            <el-button type="success" plain @click="setCheckedKeys">刚才选中了哪些NodeKey？</el-button>
          </div>

          <el-tree
            :data="treeData"
            show-checkbox
            :filter-node-method="filterNode"
            node-key="id"
            ref="tree2"
            highlight-current
            class="filter-tree"
            :props="defaultProps"
          ></el-tree>
        </div>
      </el-col>
      <el-col :span="18">
        <div>
          <el-input :placeholder="$t('orgTree.keyWordTip')" v-model="listQuery.title" style="width: 200px;" @keyup.enter.native="handleKeyWord"/>
          <el-button type="primary" @click="handleKeyWord">点击查询</el-button>
          <el-button type="info">新增用户</el-button>
          <el-button type="info">导入用户</el-button>
        </div>
        <div>
          <el-table :data="tableData" style="width: 100%">
            <el-table-column prop="date" label="日期" width="180"></el-table-column>
            <el-table-column prop="name" label="姓名" width="180"></el-table-column>
            <el-table-column prop="address" label="地址"></el-table-column>
          </el-table>
        </div>
        <pagination v-show="total>0" :total="total" :page.sync="listQuery.page" :limit.sync="listQuery.limit" @pagination="getList" />
      </el-col>
    </el-row>
  </div>
</template>

<script>
import { fetchTableList } from '@/api/org'
import { fetchOrgTree } from '@/api/org'
import Pagination from '@/components/Pagination' // Secondary package based on el-pagination
let lastFocusNodes
let lastFocusNodeKeys
export default {
  name: 'orgTree',
  components: { Pagination },
  data() {
    return {
      filterText: "",
      listQuery: {
        page: 1,
        title: undefined,
        limit: 10,
      },
      treeData: null,
      tableData: null,
      total: 0,
      defaultProps: {
        children: "children",
        label: "label"
      }
    };
  },
  watch: {
    filterText(val) {
      this.$refs.tree2.filter(val);
    }
  },
  created() {
    this.getList()
    this.getOrgTree()
    // let vm = this
  },
  methods: {
    getList() {
      fetchTableList(this.listQuery).then(response => {
        this.tableData = response.data.items
        this.total = response.data.total

        // // Just to simulate the time of the request
        // setTimeout(() => {
        //   this.listLoading = false
        // }, 1.5 * 1000)
      })
    },
    getOrgTree() {
      fetchOrgTree(this.listQuery).then(response => {
        this.treeData = response.data
        // this.total = response.data.total
      })
    },
    handleKeyWord() {
      this.listQuery.page = 1
      this.getList()
    },
    getCheckedNodes() {
      lastFocusNodes = this.$refs.tree2.getCheckedNodes()
      console.log(this.$refs.tree2.getCheckedNodes());
      alert("选择了：" + JSON.stringify(this.$refs.tree2.getCheckedNodes()))
    },
    getCheckedKeys() {
      lastFocusNodeKeys = this.$refs.tree2.getCheckedKeys()
      console.log(this.$refs.tree2.getCheckedKeys());
      alert("选择了：" + this.$refs.tree2.getCheckedKeys())
    },
    setCheckedNodes() {
      this.$refs.tree2.setCheckedNodes(lastFocusNodes)
    //   this.$refs.tree2.setCheckedNodes([
    //     {
    //       id: 207,
    //       label: "二级 2-1"
    //     },
    //     {
    //       id: 219,
    //       label: "三级 1-1-1"
    //     }
    //   ]);
    },
    setCheckedKeys() {
      this.$refs.tree2.setCheckedKeys(lastFocusNodeKeys)
      // this.$refs.tree2.setCheckedKeys([206,224,225]);
    },
    resetChecked() {
      this.$refs.tree2.setCheckedKeys([]);
    },
    filterNode(value, data) {
      if (!value) return true;
      return data.label.indexOf(value) !== -1;
    }
  }
};
</script>
